@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont;
}

code {
  font-family: monospace;
}

* {
  font-family: "Poppins", sans-serif;
}
body {
  background: #f1f2f5;
  overflow-x: hidden;
  height: 100%;
  width: 100%;
  min-height: 100vh;
}
.app__body {
  display: flex;
}

.feed {
  flex: 1 1;
  margin-left: 225px;
  margin-top: 70px;
  padding: 30px 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.messageSender {
  display: flex;
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
  width: 100%;
}

.messageSender__top {
  display: flex;
  border-bottom: 1px solid #eff2f5;
  padding-bottom: 15px;
  padding: 15px;
}

.messageSender__top form {
  flex: 1 1;
  display: flex;
}

.messageSender__top form input {
  outline-width: 0;
  border: none;
  padding: 5px 20px;
  margin: 0 10px;
  border-radius: 999px;
  background-color: #eff2f5;
}

.messageSender__top form button {
  display: none;
}

.messageSender__input {
  flex: 1 1;
}

.messageSender__bottom {
  display: flex;
  justify-content: space-evenly;
}

.messageSender__option {
  padding: 20px;
  display: flex;
  align-items: center;
  color: #555;
  transition: 0.5s;
  margin: 5px;
  cursor: pointer;
}

.messageSender__option h3 {
  font-size: medium;
  margin-left: 10px;
}

.messageSender__option:hover {
  background-color: #eff2f5;
  border-radius: 20px;
}

.post {
  width: 100%;
  margin-top: 15px;
  border-radius: 15px;
  background: #fff;
  box-sizing: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
}
.post__top {
  display: flex;
  position: relative;
  align-items: center;
  padding: 15px;
}
.post__avatar {
  margin-right: 10px;
}
.post__topInfo h3 {
  font-size: medium;
}
.post__topInfo p {
  font-size: small;
  color: #777;
}
.post__image img {
  width: 100%;
}
.post__bottom {
  margin: 10px 0;
  padding: 15px 20px;
}
.post__options {
  font-size: 18px;
  border-top: 1px solid #ddd;
  display: flex;
  justify-content: space-evenly;
  font-size: medium;
  color: #666;
  cursor: pointer;
  padding: 15px;
}
.post__option {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  flex: 1 1;
  transition: 0.5s;
}
.post__option p {
  margin-left: 18px;
}
.post__option:hover {
  background: #eff2f5;
  border-radius: 5px;
}

.story {
  position: relative;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 120px;
  height: 200px;
  box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
  border-radius: 10px;
  transition: 0.5s ease-in-out;
  cursor: pointer;
  margin: 5px;
}
.story:hover {
  transform: scale(1.07);
}
.story__avatar {
  margin: 10px;
  border: 5px solid #2e81f4;
}
.story h4 {
  position: absolute;
  bottom: 20px;
  left: 20px;
  color: #fff;
}

.storyReel {
  display: flex;
}

* {
  margin: 0;
  padding: 0;
}
.header {
  display: flex;
  padding: 15px 20px;
  justify-content: space-around;
  position: fixed;
  background: #fff;
  width: 97.3%;
  z-index: 1;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}
.header__left {
  display: flex;
  justify-content: center;
}
.header__input {
  display: flex;
  align-items: center;
  background: #eff2f5;
  padding: 10px;
  margin-left: 10px;
  border-radius: 999px;
}
.header__input input {
  border: none;
  padding: 0 10px;
  background: transparent;
  outline-width: 0;
}
.header__left img {
  height: 40px;
}
.header__center {
  display: flex;
  flex: 1 1;
  justify-content: center;
}
.header__option {
  display: flex;
  align-items: center;
  padding: 0 30px;
  cursor: pointer;
  transition: 0.5s;
}
.header__option:hover {
  background: #eff2f5;
  border-radius: 10px;
  align-items: center;
}
.header__option .MuiSvgIcon-root {
  color: #666;
}
.header__option:hover .MuiSvgIcon-root {
  color: #2e81f4;
}
.header__info {
  display: flex;
  align-items: center;
}
/* .MuiIconButton-root {
  color: #000;
} */
.header__info h4 {
  margin-left: 10px;
}
.header__right {
  display: flex;
}
.header__option--active {
  border-bottom: 4px solid #2e81f4;
}
.header__option--active .MuiSvgIcon-root {
  color: #2e81f4;
}

.login {
  display: grid;
  place-items: center;
  height: 100vh;
}
.login div img {
  object-fit: contain;
  height: 150px;
}

.login button {
  width: 300px;
  text-transform: none;
  background-color: #2e81f4;
  border: 1px solid #2e81f4;
  color: #eff2f5;
  font-size: 16px;
}
.login button:hover {
  background-color: #fff;
  color: #2e81f4;
}
.login div {
  display: flex;
  flex-direction: column;
}

.sidebar {
  padding: 25px 10px;
  flex: 0.33 1;
  position: fixed;
}

.sidebarRow {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  transition: 0.5s;
}
.sidebarRow:hover {
  background: #dfdfdf;
  border-radius: 5px;
}
.sidebarRow h4 {
  margin-left: 28px;
  font-weight: 600;
}
.sidebarRow .MuiSvgIcon-root {
  font-size: xx-large;
  color: #2e81f4;
}

.widgets {
  margin-top: 90px;
  margin-left: -80px;
}

